import React from 'react'
import { makeStyles } from '@material-ui/core/styles'
import {
  Button,
  Table as MTable,
  TableBody,
  TableCell,
  TableContainer,
  TableHead,
  TableRow,
} from '@material-ui/core'

const useStyles = makeStyles({
  table: {
    minWidth: 650,
    marginBottom: '2rem',
  },
})

export default function Table({ data, onClick }) {
  const classes = useStyles()
  const handleButton = (name, data) => {
    onClick && onClick({ name, data })
  }

  return (
    <TableContainer>
      <MTable className={classes.table} aria-label='simple table'>
        <TableHead>
          <TableRow>
            <TableCell>Name</TableCell>
            <TableCell align='right'>Phone</TableCell>
            <TableCell align='right'>Web</TableCell>
            <TableCell align='right'>Company</TableCell>
            <TableCell align='right'>Actions</TableCell>
          </TableRow>
        </TableHead>
        <TableBody>
          {data.map((row, key) => (
            <TableRow key={key}>
              <TableCell component='th' scope='row'>
                {row.name}
              </TableCell>
              <TableCell align='right'>{row.phone}</TableCell>
              <TableCell align='right'>{row.website}</TableCell>
              <TableCell align='right'>{row.company.name}</TableCell>
              <TableCell align='right'>
                <Button onClick={() => handleButton('delete', row.id)}>
                  Delete
                </Button>
              </TableCell>
            </TableRow>
          ))}
        </TableBody>
      </MTable>
    </TableContainer>
  )
}
